<template>
  <div class="count-box" @click.stop>
    <div @click="subCount()" class="sub-btn">
      <img
        :style="{'background': (subBtnDisable || disable) ? '#ccc' : '#333'}"
        alt
      />
    </div>
    <!-- <div class="count-input" :style="{'color': disable ? '#ccc' : '#333'}">{{number}}</div> -->
    <!-- <input
      type="number"
      ref='inputNum'
      class="count-input"
      :disabled="true"
      :style="{'color': disable ? '#ccc' : '#333'}"
      @input="change"
      @blur="onBlur"
      v-model='number'
    > -->
    <div class="count-input" :style="{'color': disable ? '#ccc' : '#333'}">
      {{number}}
    </div>
    <div @click="addCount()" class="add-btn">
      <img
        :src="(disable || addBtnDisable) ? addBtnDisableSrc : addBtn"
        alt
      />
    </div>
  </div>
</template>
<script>
export default {
  props: {
    disable: {
      type: Boolean,
      default: false,
    },
    itemIndex: {
      type: Number,
      default: -1,
    },
    min: {
      type: Number,
      default: 1,
    },
    max: {
      type: Number,
      default: 500,
    },
    num: {
      type: Number,
      default: 1,
    },
    hasLimit: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      number: 1,
      subBtnDisable: true,
      addBtnDisable: false,
      addBtn: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/icon/stepperAdd.png',
      addBtnDisableSrc: 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/icon/stepperAddDisable.png',
      inputContent: '',
    }
  },
  watch: {
    num: {
      handler(newVal) {
        this.number = newVal
        this.subBtnDisable = newVal === this.min;
        this.addBtnDisable = newVal >= this.max;
      },
      immediate: true
    },
    number(newVal) {
      this.subBtnDisable = newVal === this.min;
      this.addBtnDisable = newVal >= this.max;
      this.$emit('update:num', newVal)
    }
  },
  methods: {
    addCount() {
      if (this.disable || this.addBtnDisable) return;
      if (this.hasLimit) {
        this.upDateStep('add', this.number + 1);
      } else {
        this.number++;
        this.upDateStep('add', this.number);
      }
    },
    subCount() {
      if (this.disable || this.subBtnDisable) return;
      // if (this.hasLimit) {
      //   this.upDateStep('add', this.number - 1);
      // } else {
      // }
      this.number--;
      this.upDateStep('sub', this.number);
    },
    /**
     * type: add-增加  sub-减少
     */
    upDateStep(type, number) {
      this.$emit('stepperChange', number, this.itemIndex, type);
    },
    change(e) {
      this.inputContent = e.data;
    },
    onBlur(e) {
      let number = +this.inputContent;
      if (number === '') {
        this.number = this.num;
      } else if (number < this.min) {
        number = this.min;
      } else if (number > this.max) {
        number = this.max;
      }
      this.number = number;
      this.upDateStep('', this.number);
    }
  }
}
</script>
<style lang="scss" scoped>
.count-box {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.count-input {
  width: 0.74rem;
  height: 0.36rem;
  color: #333;
  font-size: 0.24rem;
  background: #f5f5f5;
  border-radius: 0.08rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.sub-btn {
  width: 0.52rem;
  height: 0.52rem;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: 0.18rem;
    height: 0.04rem;
    border-radius: .02rem;
  }
}
.add-btn {
  width: 0.52rem;
  height: 0.52rem;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    width: 0.28rem;
    height: 0.28rem;
  }
}
</style>
