        let result = res.data;
        this.hotLiveInfo = result[id] || []
      })
    },
    /**
     * 跳转直播页面
     */
    doOpenLive(info) {
      console.log(info, 'info')
      if (this.$util.isHaierApp()) {
        let isShowLive = this.$route.query.show_live
        if (isShowLive) {
          instance.initDeviceReady().then(() => {
            instance.upLiveModule.closeFloatWindow()
            instance.upVdnModule.goToPage({ url: this.$util.isIos() ? info.appIosUrl : info.appAndroidUrl });
          })
        } else {
          instance.initDeviceReady().then(() => {
            instance.upVdnModule.goToPage({ url: this.$util.isIos() ? info.appIosUrl : info.appAndroidUrl });
          });
        }
      } else {
        getUcLoginUrl({
          redirectUri: info.h5Url,
          target: userKeyTarget.LiveTarget,
          targetTest: userKeyTarget.LiveTarget_test,
          redirectUriTest: info.h5Url,
        }).then(({ data }) => {
          if (data.success) {
            location.href = data.data
          } else {
            this.$toast(data.message)
          }
        })
      }
    },
    /**
     * @description 解决方案详情
     * @param {id}
     * 返回接口请求数据
    */
    getDetail () {
      const context = this;
      const data = {
        id: context.data.id,
      }
      personalApi.getSolutionDetails(data).then(res => {
        const _resData = res;
        if (_resData.success) {
          this.programLiveInfo(_resData.data.id)
          let plans = _resData.data.plans
          context.$set(context.data, 'resData', _resData.data || {})
          /**
           * productList 存放动态变量名称
           * 通过循环拼接productsId拼接后放到productList里
           * 在把productList放到plans中
           */
          let productList = {};
          for (let i in plans) {
            let productsIds = '';
            let ab = 'pro' + i;
            productList[ab] = '';
            for (let s in plans[i].groups) {
              plans[i].groups[s].products.map((item, index) => {
                if (index === 0) {
                  productsIds = productsIds + item.productId.toString() + ',';
                  productList[ab] = productsIds
                }
              })
            }
          }
          plans.map((item, index) => {
            Object.keys(productList).map((items, indexs) => {
              if (index === indexs) {
                item.productsIds = productList[items]
              }
            })
          })
          localData.plansAll = plans
        } else {
          this.$toast.fail(_resData.message);
        }
      }).then(res => {
        /**
         * @description 解决方案清单的商品列表
        */
        context.getSequenceProductsAll();
      })
    },
    /**
     *  @description tabs点击时候修改商品和价格
     * index 导航下标
     * title 导航名称
     */
    onClickTabs (index, title) {
      const context = this;
      this.$nextTick(() => {
        let getPrice = context.$refs[`child${index}`][0].parentDatas
        context.countTotal = getPrice.countTotal
        context.priceTotal = getPrice.priceTotal
      })
    },
    /**
     * @description 通过组件选中传递价格
     * countTotal 商品数量
     * priceTotal 商品总价
    */
    allPrice(price) {
      this.countTotal = price.countTotal
      this.priceTotal = price.priceTotal
    },
    /**
     * 子组件点击获取父组件滚动高度
    */
    childscroll() {
      localData.getScrollTop = this.$refs.content_boxs.scrollTop
    },
    /**
     * @description 解决方案清单的商品列表
     * @param {moreSelectList} 更多选择 true有更多选择  false没有更多选择
     * 返回接口请求数据
     * 如果moreChoices存在的时候获取子组件拼接后的productsId
    */
    getSequenceProducts (parameter) {
      const context = this;
      let productIdMore = ''
      if (parameter && parameter.type === 'moreChoices') {
        productIdMore = parameter.deleteProductsIds
      } else {
        productIdMore = localData.plansAll[context.active].deleteProductsIds
      }

      const data = {
        productsIds: productIdMore,
        streetId: context.data.streetId,
        cityId: context.data.cityId,
        provinceId: context.data.provinceId,
        regionId: context.data.regionId,
      }
      personalApi.getSequenceProducts(data).then(res => {
        const _resData = res.data;
        // 手动清除 this.$toast
        this.$toast.clear();
        if (_resData.success) {
          this.isPlus = _resData.data.plus;
          let list = _resData.data.productDetailVOS || [];
          /**
           * 更多选择的list展示
           * 更多选择展示的时候，默认的清单数据不动
           * 默认选中原来清单中的展示项
           * 增加是否有货，是否预订状态是否可以选中
           * moreChoices的时候来操作替换商品列表
           *  */
          if (parameter && parameter.type === 'moreChoices') {
            list.map(item => {
              item.checked = false;
              if (parameter.id === item.id) {
                if (parameter.stock === false && (parameter.bookable === 0 || parameter.bookable === 1)) {
                  item.checked = false;
                } else {
                  item.checked = true;
                }
              }
            })
            this.$refs[`child${this.active}`][0].parentDatas.moreChoicesList = list
            this.$refs[`child${this.active}`][0].parentDatas.moreChoicesDialog = true
          }
        } else {
          this.$toast.fail(_resData.message);
        }
      })
    },
    /**
     * @description 方案清单的商品列表
     * 跟getSequenceProducts一样只是不处理子组件
     * transmit定义基础数据，按原有接口赋值给子组件使用
    */
    getSequenceProductsAll (parameter) {
      const context = this;
      let allListBox = [];// 拼接后的数组
      let plansAlls = localData.plansAll
      const groupProductsObject = context.data.resData.plans;
      plansAlls.forEach((item, index) => {
        const allData = {
          productsIds: item.productsIds,
          streetId: context.data.streetId,
          cityId: context.data.cityId,
          provinceId: context.data.provinceId,
          regionId: context.data.regionId,
          storeId: this.getShareId || this.storeId || '',
        }
        let transmit = {
          name: '',
          data: {
            id: '',
            storeId: '',
            provinceId: '',
            cityId: '',
            regionId: '',
            streetId: '',
            productsIds: '',
            resData: context.data.resData,
            moreChoicesList: null,
            moreChoicesCheckedIndex: null,
            moreChoicesCheckedAll: false,
            moreChoicesDialog: false,
            priceTotal: 0,
            countTotal: 0,
            resList: [],
            active: index // 在组件中获取数组的下标
          }
        };
        personalApi.getSequenceProducts(allData).then(res => {
          const _resData = res.data;
          this.$toast.clear();// 手动清除 this.$toast
          if (_resData.success) {
            this.isSkeletonLoading = false;
            this.isPlus = _resData.data.plus;
            let list = _resData.data.productDetailVOS || [];
            list.forEach(items => {
              items.checked = false
              items.moreSelectList = false;
              /**
               * 遍历每个分组列表-array
               * 如果商品的id 和 商品分组下的商品列表的productId相等，代表这个商品属于当前分组
               * 如果分组内的列表数据多于1条，就展示更多选择，否则不展示
               */
              groupProductsObject[index].groups.map((itemGroupChild, groupIndex) => {
                itemGroupChild.products.forEach((itempro, indes) => {
                  if (items.id === itempro.productId) {
                    items.moreSelectList = groupProductsObject[index].groups[groupIndex].products.length > 1;
                  }
                })
              })
            })
            /**
             * 从新对数组进行排序确保跟后台接口返回顺序一致
             * 通过index进行排序
             */
            transmit.name = item.name
            transmit.data.resList = list
            transmit.index = index
            allListBox.push(transmit)
            allListBox.sort(context.allCompareSore('index'))
            context.tablist = allListBox
          }
        })
      })
    },
    /**
     *  @description 根据index进行升序排序
     */
    allCompareSore(property) {
      return function(a, b) {
        var value1 = a[property];
        var value2 = b[property];
        return value1 - value2;
      }
    },
    /**
     * @description 判断登陆状态，未登录便登陆
     */
    isLogin() {
      const context = this;
      localData.getScrollTop = this.$refs.content_boxs.scrollTop
      // 判断登陆状态，未登录便登陆
      if (context.$util.isHaierApp()) { // 智家
        if (!this.$store.getters.getLoginStatus) {
          this.$store.dispatch('doLogin').then(res => {
            if (res !== 400) {
              context.createOrder();
            }
          })
        } else {
          context.createOrder()
        }
        // context.$store.dispatch('handleUpAuth').then((res) => {
        //   console.log(res); // return true / false
        //   if (res) {
        //     // 创建订单
        //     context.createOrder();
        //   } else {
        //     this.$toast.fail('用户信息异常');
        //   }
        // });
      } else {
        if (!this.$store.getters.getLoginStatus) {
          this.$store.dispatch('feedback', this.$route.path);
          this.$store.dispatch('doLogin');
          return
        }
        // h5
        // 创建订单
        context.createOrder();
      }
