    /**
     * @description 创建订单 商品数量为0时 不能提交订单
    */
    createOrder() {
      const context = this;
      let dataProList = [];
      // 替换选中的tab中的数据
      let getListCheck = context.$refs[`child${context.active}`][0].parentDatas.resList
      // context.data.resList.map(item => {
      console.log('getListCheck', getListCheck)
      getListCheck.map(item => {
        if (item.checked) {
          // item.num = item.num || 1;
          // item.proId = item.id;
          // item.visitStore = '';
          dataProList.push({
            num: item.num || 1,
            proId: item.id,
            name: item.name,
            visitStore: ''
          })
        }
      })
      const data = {
        ac: 1,
        ch: 61,
        proList: dataProList,
        street: context.data.streetId,
        v: 2,
        shareId: this.getShareId || context.storeId || '',
        isOrderRight: '1', // 是否需要权益 '1': 需要; '0': 不需要
      };
      console.log('下单参数:', data);
      if (data.proList.length === 0) {
        this.$toast.fail('请至少选择1种商品');
        return;
      }
      this.$router.push({
        name: 'create',
        query: {
          _proList: dataProList,
          storeId: context.storeId || '',
          ac: '1'
        },
      })
