    handleLocation (data) {
      this.showLocationModal = false;
      localData.temporaryAddress = data
      const context = this;
      context.data.streetId = data.townshipId
      context.data.cityId = data.cityId
      context.data.provinceId = data.provinceId
      context.data.regionId = data.districtId
