<template>
  <div class="list-block">
    <!-- <h3>方案</h3> -->

    <div class="title">
      <common-checkbox v-model="parentDatas.moreChoicesCheckedAll" class="left" :width="'16px'"  :height="'16px'" @toggleCheck="checkedAll(parentDatas.moreChoicesCheckedAll)"></common-checkbox>
      <div class="allSelect"><span>全部选择</span></div>
    </div>
    <ul>
      <li v-for="(item, index) in parentDatas.resList" :key = 'index'>
        <div class="commonCheckBox">
          <common-checkbox v-if="item.stock == false && item.bookable == 0 " class="left" :width="'16px'"  :height="'16px'" :disabled="true"></common-checkbox>
          <common-checkbox v-else v-model="item.checked" @onChange="checkedChange" class="left" :width="'16px'"  :height="'16px'"></common-checkbox>
        </div>
        <van-image
          lazy-load
          @click="viewDetail(item.id)"
          :src="item.imageUrl | imageToHttps | imageThumb('300') | imageToWebp($webpSurpport)"
        />
        <div class="lowerShelfBox" v-if="item.onSale === 0">
          <img alt class="lowerShelf" :src="`${$cdnImageUrl}/order/lowerShelf.png`" />
        </div>
        <div class="rightBox">
          <div class="product" @click="viewDetail(item.id)">
            <div class="product-title">{{item.name}}</div>
            <div class="introduce">{{item.title}}</div>
            <div class="price_more_box">
              <div class="price">
                ￥
                <span>{{item.price|formatPrice | formatPriceInt}}</span>
                <p style="margin-left: -.1rem; display: inline-block">{{item.price|formatPrice | formatPriceFloat}}</p>
              </div>
              <member-price v-if="!!item.plusPrice" :memberPrice="item.plusPrice" :mStyle="{'margin-left': '0.02rem'}"/>
              <div class="itSoluteDetailmore">
                <span :class="{more_instork:(item.stock == false || item.bookable==1) || (item.stock),more_instorkNo:item.stock == false && item.bookable == 0}">
                  <span class="text">{{(item.stock == false && item.bookable == 0) ?'无货':item.stock == true?'有货':(item.stock == false && item.bookable == 1)?'可预订':''}}</span>
                </span>
              </div>
            </div>
          </div>
          <div class="more" v-if="item.onSale !== 0">
            <Stepper
              :num.sync="item.num"
              :itemIndex="index"
              :disable="(item.stock == false && item.bookable == 0)?true:false"
              :min="(item.stock == false && item.bookable == 0) ? 0 : 1"
              :max="500"
              @stepperChange="fnChangeProductNumber"/>
            <van-button v-show="item.moreSelectList" class="more_replace"  type="info" size="small" @click="moreChoices(item.id, index,item.stock,item.bookable)">替换</van-button>
          </div>
        </div>
      </li>
    </ul>

    <!--更多选择的弹窗 -->
    <van-popup position='bottom' v-model="parentDatas.moreChoicesDialog">
      <div class="more-choices-dialog">
        <div class="dialog-title">
          <span class="dialog_h">替换</span>
          <van-icon name="cross" @click="moreChoicesDialogHide"/>
          <span class="dialog_p">可以替换以下同类产品</span>
        </div>

        <ul>
          <li @click="moreChoicesChecked(item)" v-for="(item, index) in parentDatas.moreChoicesList"  :key = 'index'>
          <div class="commonCheckBox">
            <common-checkbox v-if="item.stock == false && item.bookable == 0" class="left" :width="'16px'"  :height="'16px'" :disabled="true"></common-checkbox>
            <common-checkbox v-else v-model="item.checked" class="left" :width="'16px'"  :height="'16px'"></common-checkbox>
          </div>
            <van-image
              lazy-load
              :src="item.imageUrl | imageToHttps | imageThumb('300') | imageToWebp($webpSurpport)"
            />
            <div class="rightBox">
              <div class="product">
                <div class="product-title">{{item.name}}</div>
                <div class="introduce">{{item.title}}</div>
                <div class="price"><span class="symbol">￥</span>{{item.price|fmtMoneyFilter}}</div>
              </div>
              <div class="more">
                <span :class="{more_instork:(item.stock == false && item.bookable==1) || (item.stock),more_instorkNo:item.stock == false && item.bookable == 0}">{{(item.stock == false && item.bookable == 0) ?'无货':item.stock == true?'有货':(item.stock == false && item.bookable == 1)?'可预订':''}}</span>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </van-popup>

    <div class="bottom-clearance"></div>

  </div>
</template>

<script>
/**
 * tost提醒
 */
import Vue from 'vue';
import commonCheckbox from '@/components/checkbox/common-checkbox'
import { Toast, Popup, Checkbox } from 'vant';
import memberPrice from '@/components/memberPrice';
import Stepper from '@/components/common-stepper'
Vue.use({ Toast, Popup, Checkbox });
/*
 * 金额清单列表
*/
export default {
  name: 'list',
  props: {
    isShowLive: {
      type: Boolean
    },
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: Object,
      default: function () {
        return {}
      },
    },
    storeId: {
      type: String,
      default: '',
    },
    isPlus: {
      type: Boolean,
      default: false,
    }
  },
  data: function() {
    return {
      checked: false,
      parentDatas: this.$util.strJSONParse(JSON.stringify(this.parentData), {}),
      goodsNum: '0',
      productNumber: 1,
      showLive: false
    }
  },
  watch: {
    isShowLive: {
      handler(newValue, oldValue) {
        this.showLive = newValue
      },
      immediate: true
    },
    parentData: {
      handler(newValue, oldValue) {
        this.parentDatas = newValue
      },
      deep: true
    }
  },
  components: {
    Stepper,
    commonCheckbox,
    memberPrice
  },
  mounted() {
    console.log(this.parentDatas)
  },
  methods: {
    fnChangeProductNumber(value, fatchIndex) {
      let priceTotal = 0;
      let countTotal = 0;
      let resList = this.parentDatas.resList
      resList.map((item, index) => {
        // 选中并且相同情况点击了进步器
        if (item.checked) {
          if (this.isPlus && !!item.plusPrice) {
            if (index === fatchIndex) {
              item.num = value;
              priceTotal = parseFloat(priceTotal) + parseFloat(item.plusPrice) * value;// 价格
            } else {
              priceTotal = parseFloat(priceTotal) + parseFloat(item.plusPrice) * (item.num || 1);// 价格
            }
          } else {
            if (index === fatchIndex) {
              item.num = value;
              priceTotal = parseFloat(priceTotal) + parseFloat(item.price) * value;// 价格
            } else {
              priceTotal = parseFloat(priceTotal) + parseFloat(item.price) * (item.num || 1);// 价格
            }
          }
          countTotal = parseFloat(countTotal) + (item.num || 1);// 数量，数量是对的
          this.parentDatas.priceTotal = priceTotal;
          this.parentDatas.countTotal = countTotal;
          this.$emit('allPrice', { priceTotal: priceTotal, countTotal: countTotal })
        } else {
          if (index === fatchIndex) {
            item.num = value;
          }
        }
      })
    },
    /**
     * @description 跳转到查看详情页面
     * @param {}
    */
    viewDetail (productId) {
      const context = this;
      context.$emit('childscroll')
      if (this.showLive) {
        context.$router.push({
          path: `/goodsDetail?productId=${productId}&show_live=1&visitStore=${this.storeId}`,
        });
      } else {
        context.$router.push({
          path: `/goodsDetail?productId=${productId}&visitStore=${this.storeId}`,
        });
      }
    },

    /**
     * @description 全选/反选
     * @param {isCheckedAll} true全选 false不全选
     * @param {priceTotal} 商品金额合计
     * @param {countTotal} 商品数量合计
    */
    checkedAll(value) {
      const context = this;
      const list = context.parentDatas.resList;
      let isCheckedAll = value;
      let priceTotal = 0;
      let countTotal = 0;
      list.map((item, index) => {
        if ((item.stock === false && item.bookable === 0) || item.onSale === 0) {
          item.checked = false
        } else {
          item.checked = isCheckedAll;
          if (isCheckedAll) {
            priceTotal = parseFloat(priceTotal) + parseFloat(item.price) * (item.num || 1);
            countTotal = parseFloat(countTotal) + (item.num || 1);
          }
        }
      })
      context.parentDatas.priceTotal = priceTotal;
      context.parentDatas.countTotal = countTotal;
    },

    /**
     * @description 清单列表中的清单选择、 合计商品总价、已选商品数量、 判断是否需要全选或者反选
     * @param {isCheckedAll} true全选 false不全选
     * @param {priceTotal} 商品金额合计
     * @param {countTotal} 商品数量合计
     * @param {temporaryList} 空数组接收非0情况
    */
    checkedChange() {
      const context = this;
      const list = context.parentDatas.resList;
      let priceTotal = 0;
      let countTotal = 0;
      let isCheckedAll = false;
      let temporaryList = [];
      list.map((item, index) => {
        // 不是0的情况，有2个
        if (item.stock === false && item.bookable === 0) {
        } else {
          temporaryList.push(item)
          // 如果有一个是false，全选就为false
          if (item.checked === false) {
            isCheckedAll = false;
          }
          let isCheckeds = temporaryList.every(item => item.checked === true)
          if (isCheckeds) {
            isCheckedAll = true
          } else {
            isCheckedAll = false
          }
        }
        if (item.checked) {
          if (this.isPlus && !!item.plusPrice) {
            priceTotal = parseFloat(priceTotal) + parseFloat(item.plusPrice) * (item.num || 1);
          } else {
            priceTotal = parseFloat(priceTotal) + parseFloat(item.price) * (item.num || 1);
          }
          countTotal = parseFloat(countTotal) + (item.num || 1);
        }
      })
      context.parentDatas.moreChoicesCheckedAll = isCheckedAll;
      context.parentDatas.priceTotal = priceTotal;
      context.parentDatas.countTotal = countTotal;
      // 把选中价格传到页面上
      context.$emit('allPrice', { priceTotal: priceTotal, countTotal: countTotal })
    },

    /**
     * @description 清单列表中的  合计商品总价、已选商品数量
    */
    amountCalculation() {

    },

    /**
     * @description 更多商品选择
     * @param {moreChoicesDialog} 更多选择弹窗 展示/隐藏
     * @param {isChecked} 判断当前选择的 更多选择的类别
     * @param {isCheckedChild} 判断当前选择的 更多选择的类别用于map循环内 判断item.productId === id相等时的定义
    */
    moreChoices(id, index, stock, bookable) {
      const context = this;
      const active = context.parentDatas.active
      const groupProductsObj = context.parentDatas.resData.plans;
      context.parentDatas.moreChoicesCheckedIndex = index;
      let productsIds = '';
      let isChecked = false;
      let isCheckedChild = false;
      groupProductsObj[active].groups.map((item, index) => {
        productsIds = isChecked ? productsIds : '';
        item.products.map((childItem, childIndex) => {
          if (childItem.productId === id) {
            isCheckedChild = true;
          }
          if (!isChecked) {
            productsIds = productsIds + childItem.productId.toString() + ',';
          }
        })
        isChecked = isCheckedChild;
      })
      // 删除本身id，不查询
      let deleteProductsIds = productsIds.replace(id + ',', '')
      context.parentDatas.productsIds = deleteProductsIds;
      // 调用父组件 获取更多选择商品的方法
      /**
       * @param {id} 传item.id
       * @param {stock} 有货true 无货false
       * @param {bookable} 是否能预订0,1
       * @param {productsIds} 传productsIds
       */
      context.$emit('fatherMethod', { type: 'moreChoices', id: id, stock: stock, bookable: bookable, deleteProductsIds })
    },

    /**
     * @description 更多商品选择弹窗 关闭隐藏
    */
    moreChoicesDialogHide () {
      const context = this;
      context.parentDatas.moreChoicesDialog = false;
    },

    /**
     * @description 更多商品选择弹窗 选择商品后隐藏
     */
    moreChoicesChecked (item) {
      const context = this;
      /**
     * @description 更多商品选择弹窗 选择商品后隐藏
     * 无货的时候不允许替换
     */

      if (item.stock === false && item.bookable === 0) {
        return
      }
      context.parentDatas.moreChoicesList.map(listItem => {
        listItem.checked = false;
        if (listItem.id === item.id) {
          listItem.checked = true;
        }
      })
      const _index = context.parentDatas.moreChoicesCheckedIndex;
      item.moreSelectList = context.parentDatas.resList[_index].moreSelectList;
      context.parentDatas.resList[_index] = item;

      // 关闭更多选择的弹窗
      context.moreChoicesDialogHide();

      // 成套商品的清单切换后 金额要变动
      context.checkedChange();
    }
  },
}
</script>

